export default{
	props:{
		//数据
		dataList:Array,
		//宽度
		itemW:{
			type:String,
			default:"375rpx"
		},
		//高度
		bigH:{
			type:String,
			default:"375rpx"
		},
		//是否换行
		warp:{
			type:String,
			default:"wrap"
		},
		//商品文字大小
		nameSize:{
			type:String,
			default:"26rpx"
		}
	},
	methods:{
		goDetails(){
			uni.navigateTo({
				url:"/pages/details/details"
			})
		}
	}
}
